import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { Roles } from 'Shared/constants/User'
import { logout, loggedIn } from 'Shared/ducks/user/index'
import { fetchEmployer } from 'Shared/ducks/user/employer'
import ImpersonateUtility from 'Shared/components/ImpersonateUtility'

const mapStateToProps = (state, ownProps) => ({
  user: state.user,
  role: Roles.employer.name,
  redirectPath: `/employer/job-offer/${ownProps.match.params.jobOfferId}/applicants/undecided`
})

const mapDispatchToProps = dispatch => ({
  loggedIn: (data) => dispatch(loggedIn(data)),
  logout: () => dispatch(logout()),
  fetchUserById: id => dispatch(fetchEmployer(id))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ImpersonateUtility))



